
//	Font Weights
$font-weight-light: 100;
$font-weight-base:  300;
$font-weight-bold:  500;

//	Basic elements configuration
$font-size-base:     16;
$font-size-x-small:  12;
$font-size-small:    14;
$font-size-large:    20;
$font-size-x-large:  22;

//  Line heights
$line-height-base:     22;
$line-height-xx-small: $line-height-base * 0.25;
$line-height-x-small:  $line-height-base * 0.5;
$line-height-small:    $line-height-base * 0.75;
$line-height-large:    $line-height-base * 1.25;
$line-height-x-large:  $line-height-base * 1.5;
$line-height-xx-large: $line-height-base * 2;

//  Line lengths
$line-length-max:      40em;

//  H1
$font-size-h1:       48;
$font-weight-h1:     $font-weight-bold;
$line-height-h1:     48;
$margin-bottom-h1:   14;

//  H2
$font-size-h2:       36;
$font-weight-h2:     $font-weight-bold;
$line-height-h2:     43;
$margin-bottom-h2:   14;

//  H3
$font-size-h3:       24;
$font-weight-h3:     $font-weight-bold;
$line-height-h3:     29;
$margin-bottom-h3:   16;

//  H4
$font-size-h4:       18;
$font-weight-h4:     $font-weight-bold;
$line-height-h4:     19;
$margin-bottom-h4:   16;

//  H5
$font-size-h5:       14;
$font-weight-h5:     $font-weight-bold;
$line-height-h5:     17;
$margin-bottom-h5:   14;

//  H6
$font-size-h6:       11;
$font-weight-h6:     $font-weight-bold;
$line-height-h6:     17;
$margin-bottom-h6:   14;
